import { withThemeFromJSXProvider } from '@storybook/addon-styling';
import { createGlobalStyle, ThemeProvider } from 'styled-components';

import baseStyles from '#theme/baseStyles';
import { indyFonts } from '#theme/fontFace';
import * as themes from '#theme/themes';

/**
 * Adds global styles to stories
 */
const GlobalStyles = createGlobalStyle`
  ${indyFonts}
  ${baseStyles}

  body {
    background: ${({ theme }) => theme.color.canvas.base};
  }
`;

export default withThemeFromJSXProvider({
  themes,
  defaultTheme: 'base',
  Provider: ThemeProvider,
  GlobalStyles,
});
