import { DocsContainer } from '@storybook/addon-docs';
import { styled, createGlobalStyle, ThemeProvider } from 'styled-components';

import { PageHeader } from '#pages/components/Page';

import baseStyles from '#theme/baseStyles';
import { indyFonts } from '#theme/fontFace';
import { base as baseTheme } from '#theme/themes';

const GlobalStyles = createGlobalStyle`
  ${indyFonts}
  ${baseStyles}

  body {
    font: normal 1rem/1.2rem ${({ theme }) => theme.fontFamily.primaryFont};
  }

  h1, h2, h3, h4, h5, h6 {
    font: bold 1rem ${({ theme }) => theme.fontFamily.primaryFont};
  }

  p {
    font: normal 1rem/1.25rem ${({ theme }) => theme.fontFamily.secondaryFont};
  }

  .sbdocs-title {
    font: bold 2.75rem/3rem ${({ theme }) =>
      theme.fontFamily.primaryFont} !important;
  }

  .sb-anchor {
    p {
      font: normal 16px/20px ${({ theme }) => theme.fontFamily.secondaryFont};
    }
  }
`;

const Title = styled(PageHeader)``;

const Main = styled.main``;

/**
 * Adds global styles to preview manager
 */
const withPreviewStyles = ({ children, context }) => {
  const title = context.attachedCSFFile.meta.title.split('/').slice(-1);
  const isPrinciple =
    context.attachedCSFFile.meta?.tags?.includes('stories-mdx');

  return (
    <DocsContainer context={context}>
      <ThemeProvider theme={baseTheme}>
        <GlobalStyles />
        <div className="sb-unstyled">
          {isPrinciple && <Title>{title}</Title>}
          <Main>{children}</Main>
        </div>
      </ThemeProvider>
    </DocsContainer>
  );
};

export default withPreviewStyles;
